import React from "react"
import { MyUI } from "@macashipo/mlib"
import useConfigTableCell from "../../hooks/useConfigTableCell"
import styled from "styled-components"
import { FlowGetConfigMyUI } from "src/helpers/Flows/TableCell"
const WrapCell = styled.div`
  display: flex;
  & > textarea {
    border: 0;
    outline: none;
    resize: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    min-height: 100%;
    height: 100%;
    background: transparent;
    &:focus {
      background: var(--color-bg-focus);
    }
  }
`

export default function MTCWithMyUI(props) {
  const ctc = useConfigTableCell({ props })
  // console.warn("render MTCWithMyUI:", props, ctc)
  const configMyUI = FlowGetConfigMyUI({ ...ctc })
  return (
    <WrapCell
      className={ctc.classNameWrap("mtc-with_myui")}
      style={ctc.styleWrap()}
    >
      <MyUI
        {...props}
        type={configMyUI.type || "notypemyui"}
        ctc={ctc}
        data={configMyUI.dataIsRow ? ctc.row : props.cell}
        row={props.row}
        config={configMyUI}
      />
    </WrapCell>
  )
}
